import React from 'react';
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import {
    Box,
    Button,
    Card,
    CardContent,
    Slider,
    TextField,
    Typography,
} from '@mui/material';

import {
    selectPossiblePoints,
    selectTotalPoints,
} from 'pages/Test/Test.selectors';
import { sendAnswers } from 'pages/Test/Test.slice';
import { useAppDispatch, useAppSelector } from 'redux/hooks';
import { type QuizAnswers } from 'types/answers.types';

import { hardMarks, qualityMarks, understandMarks } from './QuizTest.constants';

const QuizTest = () => {
    const dispatch = useAppDispatch();

    const navigate = useNavigate();

    const { register, handleSubmit } = useForm<QuizAnswers>();

    const totalPoints = useAppSelector(selectTotalPoints);
    const possiblePoints = useAppSelector(selectPossiblePoints);

    const onSubmit = handleSubmit((data) => {
        dispatch(sendAnswers(data)).then(() => {
            navigate('/home');
        });
    });
    return (
        <>
            <Box component="form" onSubmit={onSubmit}>
                <Card sx={{ width: 770, marginTop: 2 }}>
                    <CardContent>
                        <Typography textAlign="center" variant="h6">
                              {totalPoints}  {possiblePoints} 
                        </Typography>
                    </CardContent>
                </Card>
                <Card sx={{ width: 770, marginTop: 2 }}>
                    <CardContent>
                        <Typography
                            textAlign="center"
                            sx={{ marginBottom: 1 }}
                            variant="subtitle1"
                        >
                                   ?
                        </Typography>
                        <Box
                            sx={{ width: 570, display: 'flex', margin: 'auto' }}
                        >
                            <Slider
                                {...register('hard')}
                                aria-label="Custom marks"
                                defaultValue={5}
                                step={1}
                                valueLabelDisplay="auto"
                                marks={hardMarks}
                                min={0}
                                max={10}
                            />
                        </Box>
                    </CardContent>
                </Card>
                <Card sx={{ width: 770, marginTop: 2 }}>
                    <CardContent>
                        <Typography
                            textAlign="center"
                            sx={{ marginBottom: 1 }}
                            variant="subtitle1"
                        >
                                 ?
                        </Typography>
                        <Box
                            sx={{ width: 570, display: 'flex', margin: 'auto' }}
                        >
                            <Slider
                                aria-label="Custom marks"
                                {...register('quality')}
                                defaultValue={5}
                                step={1}
                                valueLabelDisplay="auto"
                                marks={qualityMarks}
                                min={0}
                                max={10}
                            />
                        </Box>
                    </CardContent>
                </Card>
                <Card sx={{ width: 770, marginTop: 2 }}>
                    <CardContent>
                        <Typography
                            textAlign="center"
                            sx={{ marginBottom: 1 }}
                            variant="subtitle1"
                        >
                                ?
                        </Typography>
                        <Box
                            sx={{ width: 570, display: 'flex', margin: 'auto' }}
                        >
                            <Slider
                                aria-label="Custom marks"
                                defaultValue={5}
                                {...register('underStand')}
                                step={1}
                                valueLabelDisplay="auto"
                                marks={understandMarks}
                                min={0}
                                max={10}
                            />
                        </Box>
                    </CardContent>
                </Card>
                <Card sx={{ width: 770, marginTop: 2 }}>
                    <CardContent>
                        <Typography
                            textAlign="center"
                            sx={{ marginBottom: 1 }}
                            variant="subtitle1"
                        >
                                  
                            ?
                        </Typography>
                        <Box>
                            <TextField
                                {...register('possibilities')}
                                multiline
                                fullWidth
                            />
                        </Box>
                    </CardContent>
                </Card>
                <Card sx={{ width: 770, marginTop: 2 }}>
                    <CardContent>
                        <Typography
                            textAlign="center"
                            sx={{ marginBottom: 1 }}
                            variant="subtitle1"
                        >
                                 ?
                        </Typography>
                        <Box>
                            <TextField
                                {...register('result')}
                                multiline
                                fullWidth
                            />
                        </Box>
                    </CardContent>
                </Card>
                <Button
                    fullWidth
                    type="submit"
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                >
                    
                </Button>
            </Box>
        </>
    );
};

export default QuizTest;
