import { type FC } from 'react';
import { Card, CardContent, Typography } from '@mui/material';

import { type IndividualAnswersType } from './IndividualAnswers.types';

const IndividualAnswers: FC<IndividualAnswersType> = ({ answer, test }) => {
    return (
        <>
            <Card
                sx={{
                    width: '770px',
                    marginTop: 2,
                }}
            >
                <CardContent>
                    <Typography variant="subtitle1">
                        : {answer.userEmail}
                    </Typography>
                    <Typography variant="subtitle1">
                        : {answer.sex ? '' : ''}
                    </Typography>
                    <Typography variant="subtitle1">
                        : {answer.age}
                    </Typography>
                    {test.withPoints && (
                        <Typography variant="subtitle1">
                            : {answer.points}
                        </Typography>
                    )}
                    <Typography variant="subtitle1">
                         : {(answer.time / 1000).toFixed(2)}{' '}
                        c.
                    </Typography>
                    <Typography variant="subtitle1">
                           :{' '}
                        {test &&
                            (
                                answer.time /
                                test.questions.length /
                                1000
                            ).toFixed(2)}{' '}
                        c.
                    </Typography>
                </CardContent>
            </Card>
            {test.questions.map((question, index) => {
                return (
                    <Card
                        key={Math.random()}
                        sx={{
                            width: '770px',
                            marginTop: 2,
                        }}
                    >
                        <CardContent>
                            <Typography textAlign="center" variant="h6">
                                {question.question}
                            </Typography>
                            {typeof answer.answers[index] === 'object' &&
                                // @ts-expect-error
                                answer.answers[index].map((ans) => {
                                    return (
                                        <Typography
                                            key={Math.random()}
                                            variant="subtitle1"
                                        >
                                            {ans}
                                        </Typography>
                                    );
                                })}
                            {typeof answer.answers[index] === 'string' && (
                                <Typography
                                    key={Math.random()}
                                    variant="subtitle1"
                                >
                                    {answer.answers[index]}
                                </Typography>
                            )}
                        </CardContent>
                    </Card>
                );
            })}
        </>
    );
};

export default IndividualAnswers;
